@import url(https://fonts.googleapis.com/css2?family=Odibee+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --primary-white: #fff;
  --primary-blue: #030d97;
  --primary-dark: rgba(255,255,255,0);
  --primary-black: #000;
}

body {
  font-family: "Odibee Sans", sans-serif;
}

nav a.nav-link {
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

a.navbar-brand {
  font-size: 1.5rem;
  max-width: 17.25rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  height: 3rem !important;
}

.navbar-nav > li > a {
  margin-right: 0.5rem;
  margin-left: 0.5rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid rgba(255,255,255,0);
  border-top: 0.1875rem solid var(--primary-dark);
}

.navbar-nav > li > a.active, .navbar-nav > li > a.active:focus {
  border-top: 0.1875rem solid #fff;
  border-top: 0.1875rem solid var(--primary-white);
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid #fff;
  border-top: 0.1875rem solid var(--primary-white);
}

button > span.navbar-toggler-icon {
  color: #fff !important;
  color: var(--primary-white) !important;
}

@media(max-width: 500px) {
  .navbar-brand {
    width: 7rem !important;
  }
  
}


/* Header */

.header-wraper{
  position: relative;
  background: url(/Portfolio-Project/static/media/background.53bc5d92.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 90vh;
}

.main-info{
  display: -webkit-flex;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  font-family: "Odibee Sans", sans-serif;
  z-index: 1;
}

.main-info h1{
  margin-top: 10px;
  font-size: 3rem;
  color: #fff;
  color: var(--primary-white);
  text-transform: uppercase;
}

@media(max-width: 500px) {
  .main-info h1 {
    font-size: 2rem !important;
  }
  .typed-text{
    font-size: 1.5rem !important;
  }
}

/* Typed Text */

.typed-text{
  font-size: 2rem;
  color: #fff;
  color: var(--primary-white);
}

/* Main Button */
.button-main{
  border: 2px solid #000;
  border: 2px solid var(--primary-black);
  text-transform: uppercase;
  border-radius: 0;
  padding: 8px 10px 10px 10px;
  color: #fff;
  color: var(--primary-white);
  margin: 2rem;
  text-decoration: none;
}

.button-main:hover {
  text-decoration: none;
  color: #000;
  color: var(--primary-black);
  transition: .2s ease-in-out;
}

/* Particles */

.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index:1;
}

@media(max-width: 500px) {
  .particles-canvas {
    display: none;
  }
}
